*,*::before,*::after{
    box-sizing: border-box;
}

body{
    font-family: -apple-system, BlinkMacSystemFont,"Roboto Mono", 'Noto Sans TC',"Microsoft JhengHei","Helvetica Neue", monospace, Arial, sans-serif;
    line-height: 1.5;
}

.container{
    max-width: 1296px;
    margin: 0 auto;
    padding-left: 36px;
    padding-right: 36px;
}

img{
    width: 100%;
    height: auto;
    vertical-align: middle;
    &:hover{
        opacity: 80%;
    }
}

h1{
    font-size: $font-size-title;
    font-weight: 900;
}

h2{
    font-size: $font-size-subtitle;
    font-weight: 700;
}

h3{
    font-size: $font-size-subtitle;
}

p{
    &.title{
        font-size: $font-size-base-m;
    }
    &.sub{
        color: $secondary-text-color;
    }
}

a{
    text-decoration: none;
    &:hover{
        opacity:80%;
    }
}

.d-flex{
    display: flex;
    &.jcc{
        justify-content: center;
    }
    &.jcfe{
        justify-content: flex-end;
    }
    &.jcsd{
        justify-content: space-around;
    }
    &.jcsb{
        justify-content: space-between;
    }
    &.aic{
        align-items: center;
    }
    &.fxww{
        flex-wrap: wrap;
    }
}

.tac{
    text-align: center;
}

.px{
    &2{
        padding-left: 0.5rem;
        padding-right: 0.5rem;
    }
    &6{
        padding-left: 1.5rem;
        padding-right: 1.5rem;
    }
    &8{
        padding-left: 2rem;
        padding-right: 2rem;
    }
}

.pb{
    &8{
        padding-bottom: 2rem;
    }
}

.mx{
    &-2{
        margin-left: -0.5rem;
        margin-right: -0.5rem;
    }
    &-6{
        margin-left: -1.5rem;
        margin-right: -1.5rem;
    }
    &-8{
        margin-left: -2rem;
        margin-right: -2rem;
    }
}

.mt{
    &6{
        margin-top: 1.5rem;
    }
}
.mb{
    &1{
        margin-bottom: 0.25rem;
    }
    &2{
        margin-bottom: 0.5rem;
    }
    &3{
        margin-bottom: 0.75rem;
    }
    &4{
        margin-bottom: 1rem;
    }
    &5{
        margin-bottom: 1.25rem;
    }
    &6{
        margin-bottom: 1.5rem;
    }
    &7{
        margin-bottom: 1.75rem;
    }
    &8{
        margin-bottom: 2rem;
    }
    &10{
        margin-bottom: 2.5rem;
    }
    &12{
        margin-bottom: 3rem;
    }
    &14{
        margin-bottom: 3.5rem;
    }
    &17{
        margin-bottom: 4.25rem;
    }
    &24{
        margin-bottom: 6rem;
    }
}